<template>
  <el-table :data=PostListTable>
    <el-table-column type="expand">
      <template scope="props">
        <el-form inline class="demo-table-expand">
          <el-row class="tac">
            <el-col :span="24">
              <el-form-item label="">
                <span>{{ props.row.Content }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item label="">
                <span>{{ props.row.Process }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item label="">
                <span>{{ props.row.Assessment }}</span>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item label="">
                <span>{{ props.row.Feeling }}</span>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column prop="pk" label="">
    </el-table-column>
    <el-table-column prop="ClubName" label="">
    </el-table-column>
    <el-table-column prop="LinkmanName" label="">
    </el-table-column>
    <el-table-column prop="Date1" label="">
    </el-table-column>
    <el-table-column prop="Date2" label="">
    </el-table-column>
    <el-table-column prop="Region" label="">
    </el-table-column>
    <el-table-column prop="Stars" label="">
    </el-table-column>
    <el-table-column label="" v-if="user === 'CD' ">
      <template scope="scope">
        <el-rate v-on:change="StarSubmit(scope.row.pk, scope.row.Stars)" v-model="scope.row.Stars"></el-rate>
      </template>
    </el-table-column>
    <el-table-column label="" v-if="user === 'CD' && type != 'UnPassed'">
      <template scope="scope">
        <el-button size="small" type="danger" @click="DenySubmit(scope.row.pk)">
          
        </el-button>
      </template>
    </el-table-column>
    <el-table-column label="" v-if="user === 'CD' && type === 'UnPassed'">
      <template scope="scope">
        <el-button size="small" type="danger" @click="UndoDenySubmit(scope.row.pk)">
          
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        PostListTable: []
      }
    },
    props: {
      type: {
        'default': 'UnStared'
      },
      user: {
        'default': 'Club'
      }
    },
    methods: {
      StarSubmit (postid, star) {
        axios({
          method: 'POST',
          url: 'http://127.0.0.1:8000/api/post/star',
          data: JSON.stringify({
            Stars: star,
            PostId: postid,
            Token: this.GetToken
          }.bind(this))
        }).then(function (response) {
          if (response.data.message === 'success') {
            this.$notify({
              title: '',
              message: ' Id:' + postid,
              type: 'success'
            })
          } else {
            this.$notify.error({
              title: '',
              message: ''
            })
          }
        }.bind(this))
      },
      DenySubmit (postid) {
        axios({
          method: 'POST',
          url: 'http://127.0.0.1:8000/api/post/operate',
          data: JSON.stringify({
            PostId: postid,
            Token: this.GetToken,
            Operation: 'Deny'
          })
        }).then(function (response) {
          if (response.data.message === 'success') {
            this.$notify({
              title: '',
              message: ' Id:' + postid,
              type: 'success'
            })
          } else {
            this.$notify.error({
              title: '',
              message: ''
            })
          }
        }.bind(this))
      },
      UndoDenySubmit (postid) {
        axios({
          method: 'POST',
          url: 'http://127.0.0.1:8000/api/post/operate',
          data: JSON.stringify({
            PostId: postid,
            Token: this.GetToken,
            Operation: 'UndoDeny'
          })
        }).then(function (response) {
          if (response.data.message === 'success') {
            this.$notify({
              title: '',
              message: ' Id:' + postid,
              type: 'success'
            })
          } else {
            this.$notify.error({
              title: '',
              message: ''
            })
          }
        }.bind(this))
      }
    },
    computed: {
      Authenticate () {
        return this.$store.state.Authenticated
      },
      GetUserName () {
        return this.$store.state.UserName
      },
      GetToken () {
        return this.$store.state.Token
      }
    },
    mounted: function () {
      axios({
        method: 'POST',
        url: 'http://127.0.0.1:8000/api/post/list',
        data: JSON.stringify({
          Type: this.type,
          Token: this.GetToken
        })
      }).then(function (response) {
        if (response.data.message === 'success') {
          this.PostListTable = JSON.parse(response.data.data)
        } else {
          this.$notify.error({
            title: '',
            message: ''
          })
        }
      }.bind(this))
    }
  }
</script>



